  color: #2c3e50;
  padding: 20px;
}

.main-layout {
  display: flex;
  flex: 1;
  overflow-y: auto;
  margin-top: 60px;
  min-width: 90vw;
}

.content-area {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow-y: auto;
}

</style>

